






















import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

import {
  IsEmail, MinLength, validate, validateSync, ValidationError, IsDefined,
} from 'class-validator';

import { apiRaw } from '@/utilities/api';

class LoginSession {
  @IsEmail()
  email: string = '';

  @MinLength(1)
  password: string = '';

  parameterValid(parameter: string): boolean {
    const paramErrors: ValidationError[] = validateSync(this);
    return !paramErrors.filter(error => error.property === parameter).length;
  }
}

const login = async (session: LoginSession) => apiRaw.post('sessions', {
  data: {
    type: 'sessions',
    attributes: {
      email: session.email,
      password: session.password,
    },
  },
});

@Component
export default class LoginSignup extends Vue {
  submitted: boolean = false

  successful: boolean = false

  invalid: boolean = false

  submitError: boolean = false

  loading: boolean = false

  session: LoginSession = new LoginSession()

  formTitle: string = 'Login'

  formSubtitle: string = 'Please enter your email and password to login.'

  emailPlaceholder: string = 'Your email'

  passwordPlaceholder: string = 'Your password'

  async sessionValid(): Promise<boolean> {
    const errors: ValidationError[] = await validate(this.session);
    return !errors.length;
  }

  async submitUser() {
    this.submitted = true;
    this.invalid = !(await this.sessionValid());

    if (this.invalid) {
      return;
    }

    this.submitted = false;
    this.submitError = false;
    this.loading = true;

    try {
      const response = await login(this.session);
      this.successful = true;
    } catch (e) {
      this.submitError = true;
    }

    this.loading = false;
  }
}
